import React from "react";
import { HashLink as Link } from 'react-router-hash-link';

class HeaderComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      menuOpen:false
    }
  }

  toggleMenu(){
    this.setState({
      menuOpen:!this.state.menuOpen
    })
  }

  render() {
   
    return(
        <div className="banana-menu-holder">
        <header className="stencil-font ">
          <div className="header-logo">
            <Link to="/#banana-republic">BananaRepublic.</Link>
          </div>

          {this.state.menuOpen?
          
            <div className="banana-mobile-items-holder">
              <div className="banana-mobile-menu-row">
                <div className="header-button5 banana-connect banana-mobile">
                  {window.user == undefined?
                    <a onClick={()=>{window.ethereum.enable().then(()=>window.location.reload()) }}>Connect</a>
                    :
                    <a className="banana-claim" onClick={()=>{this.props.claim() }}>Claim Rewards</a>
                  }
                </div>
               
                <div className="header-button4 banana-team-menu banana-mobile">
                  <Link to="/#banana-team" onClick={()=>this.toggleMenu()}>Team</Link>
                </div>
                <div className="header-button3 banana-roadmap-menu banana-mobile">
                  <Link to="/#banana-roadmap" onClick={()=>this.toggleMenu()}>Roadmap</Link>
                </div>
                <div className="header-button2 banana-nfts-menu banana-mobile">
                  <Link to="/#banana-nfts" onClick={()=>this.toggleMenu()}>NFTs</Link>
                </div>
                <div className="header-button1 banana-mint-menu banana-mobile">
                  <Link to="/#banana-mint" onClick={()=>this.toggleMenu()}>Mint</Link>
                </div>
                <div className=" banana-twitter-menu banana-mobile">
                  <a>&nbsp;</a>
                </div>
                <div className="banana-discord-menu banana-mobile">
                  <a> &nbsp;</a>
                </div>
              </div>
            </div>
        
          : null}


          <div className="header-button5 banana-connect banana-desktop">
              {window.user == undefined?
                <a onClick={()=>{window.ethereum.enable().then(()=>window.location.reload()) }}>Connect</a>
                :
                <a  className="banana-claim-desktop" onClick={()=>{this.props.claim() }}>Claim Rewards</a>
              }
          </div>
          <div className=" banana-twitter-menu banana-desktop">
            <a>&nbsp;</a>
          </div>
          <div className="banana-discord-menu banana-desktop">
            <a> &nbsp;</a>
          </div>
          <div className="header-button4 banana-team-menu banana-desktop">
            <Link to="/#banana-team">Team</Link>
          </div>
          <div className="header-button3 banana-roadmap-menu banana-desktop">
            <Link to="/#banana-roadmap">Roadmap</Link>
          </div>
          <div className="header-button2 banana-nfts-menu banana-desktop">
            <Link to="/#banana-nfts">NFTs</Link>
          </div>
          <div className="header-button1 banana-mint-menu banana-desktop">
            <Link to="/#banana-mint">Mint</Link>
          </div>
          <div className="banana-burguer-menu banana-mobile" ><a onClick={()=>this.toggleMenu()}>≡</a></div>

       </header>
        <div className="banana-div-1"></div>
        </div>
    )
  }
}

export default HeaderComponent;
